import myeditor from "@/components/robin-editor/editor.vue";
import { mapGetters } from "vuex";
export default {
    components: {
        myeditor
    },
    data() {
        return {
            html: "测试",
            doType: "",
            tags: []
        };
    },
    onLoad(options) {
        const type = options.type;
        this.doType = type;
    },
    computed: {
        ...mapGetters(["isLogin"])
    },
    onShow() {
        let timer = setTimeout(() => {
            if (!this.isLogin) {
                uni.switchTab({
                    url: "../myblog/myblog"
                });
            }
            clearTimeout(timer);
        }, 1000);
    },
    methods: {
        hideEditor(e) {
            console.log(1);
        },
        saveEditor(context) {
            this.doType === "add" && this.addBlog(context);
            this.doType === "edit" && this.editBlog(context);
        },
        async addBlog(context) {
            delete context.errMsg;
            let html = context.html.replace(
                /(?<=src="|src=').*?(?="|')/gi,
                e => {
                    for (let i = 0; i < this.tags.length; i++) {
                        if (this.tags[i].path === e) {
                            return this.tags[i].thumbnailID;
                        }
                    }
                }
            );

            context.html = html;
            let data = { context, tags: this.tags };
            let res = await this.$http("/blog-service/add-blog", "POST", data);
            console.log(res);
            uni.switchTab({
                url: "../myblog/myblog"
            });
        },
        async editBlog(context) {
            console.log(context);
        },
        async uploadImg(path, fn) {
            let token = uni.getStorageSync("BLOG_TOKEN");
            let data = await this.$upload(path, "blogIMG");
            let thumbnailID = data._id;
            let tag = {
                path,
                thumbnailID
            };





            this.$parent.tags.push(tag);


            fn(path);
        }
    }
};
